const state = {
    name: '',
    top: null,              // top,
    type: 'rank',           // rank, 
    info: null              // , 
}

const mutations = {
    // , state.top, 
    set_detail_top (state, payload) {
        state.top = payload
    },
    // rank, 
    set_detail_type (state, payload) {
        state.type = payload
    },
    // title,
    set_detail_info (state, payload) {
        state.info = payload
    }
}

const detail={
    state,mutations
}

export default detail



