// import jsonp from '../../../node_modules/jsonp/index.js'
import jsonp from 'jsonp'

export function jsp (url, data, param) {
  // 1: ==>>dataurl
  url = url + '?' + dataString(data)
  // 2: 
  return new Promise((resolve, reject) => {
    jsonp(url, param, (err, data) => {
      if (!err) {
        resolve(data)
      } else {
        console.log('')
        reject(err)
      }
    })
  })
}

export function dataString (data) {
  let url = ''
  for (var k in data) {
    url += '&' + k + '=' + encodeURIComponent(data[k])    // encodeURIComponent() URI 
  }
  return url.substring(1)   // '&'==>>==>>&id=1&age=22==>>id=1&age=22
}


