import {get_lyric} from '../tools/api/lyric.js'

const state = {
    name: '',
    play_list: [],                      // 
    cur_song: null,                     // --->>player--->> v-if="cur_song"
    is_playing: false,                  //  or 
    deg: 0,                             // CD
    timer: null,                        // CD
    play_type: 0,                       //  :  or  ......
    song_list_show: false,              // 
    big_show: true,                     // 
    mini_show: false,
    ing_time: 0,                        // --->> , 
    end_time: 0,                        // 
    turn_time: 0,                       // 
    is_finger_moving: false             // --->> , get_palyer_time()
}

const mutations = {
    // , 
    change_song (state, payload) {
        state.cur_song = payload
        // 2 : cd 
        state.deg = 0                                   // 
        clearInterval(state.timer)                      // --->> 
        state.timer = setInterval(()=>state.deg++, 50)  // ,  50ms  1deg
        // 4 : true
        state.is_playing = true
    },
    // card, , 
    add_song (state, payload) {
        // 0 : --->>--->> song = {lyric, songname, songmid, singers, ...}
        let lyric
        get_lyric(payload.songmid).then(res=>{
            lyric=res
            let song = {lyric, ...payload}
            // 1 : 
            let list = state.play_list
            list = [song, ...list]                          // 
            list = no_repeat(list)                          // 
            state.play_list = list
            // 2 : cur_song
            state.cur_song = song
            // 3 : cd 
            state.deg = 0                                   // 
            clearInterval(state.timer)                      // --->> 
            state.timer = setInterval(()=>state.deg++, 50)  // ,  50ms  1deg
            // 4 : true
            state.is_playing = true
            // 5 : song_list.length=1, , 
            if (state.play_list.length===1) {
                state.big_show = true
                state.mini_show = false
            }
            else {
                state.big_show = false
                state.mini_show = true
            }
        })     
    },
    //   or  
    play_pause (state, payload) {
        // 1 : 
        state.is_playing = payload
        // 2 : --->> CD
        if (payload) {
            state.timer = setInterval(()=>state.deg++, 50)
        }
        else {
            clearInterval(state.timer)
        }
    },
    // 
    change_play_type (state, payload) {
        let num = state.play_type
        num++
        if (num>3) num=0
        state.play_type = num
    },
    //  X , 
    close_song_list (state, payload) {
        state.song_list_show = false    // 
        state.mini_show = false
    },
    // , 
    clear_song_list (state, payload) {
        state.play_list = []
    },
    // 
    list_show_hidden (state, payload) {
        state.song_list_show = payload
    },
    // --->> : 1, --->> 2,  
    del_one_song (state, payload) {
        let cur_index = payload                                             // , listindex
        let next_index = get_next_song_index(cur_index, state.play_list)    // index
        if (state.play_list.length===1) {
            state.cur_song = state.play_list[cur_index]                     // , 
        }
        else {
            state.cur_song = state.play_list[next_index]                    // 
        }
        state.play_list.splice(cur_index, 1)                                // 
    },
    // 
    pre (state, payload) {
        state.deg = 0
        let list = state.play_list
        // 1 : cur_songindex
        let cur_index = get_cur_song_index(state.cur_song, list)
        // 2 : index
        let pre_index = --cur_index
        if (pre_index<0) pre_index = list.length-1
        // 3 : 
        state.cur_song = state.play_list[pre_index]  
        state.is_playing = true      
    },
    // 
    next (state, payload) {
        state.deg = 0
        let list = state.play_list
        // 1 : cur_songindex
        let cur_index = get_cur_song_index(state.cur_song, list)
        // 2 : index
        let next_index = ++cur_index
        if (next_index>list.length-1) next_index=0
        // 3 : 
        state.cur_song = state.play_list[next_index]        
        state.is_playing = true      
    },
    //  : --->> type, 
    ended (state, payload) {
        state.deg = 0
        let type = state.play_type
        let list = state.play_list
        // 0 : , false
        state.is_playing = false
        // 1 : cur_songindex
        let cur_index = get_cur_song_index(state.cur_song, list)
        // 2 : index
        let next_index
        if (type===0) {         // 
            next_index = get_next_song_index(cur_index, list)
        }
        else if (type===1) {    // 
            next_index = cur_index
        }
        else if (type===2) {    // 
            next_index = cur_index+1
            if (next_index>list.length-1)    // , 
            {
                next_index = list.length-1
                state.is_playing = false    // is_palying=false, cion, 
                clearInterval(state.timer)
            }
        }
        else if (type===3) {    // 
            let num = list.length
            next_index = cur_index
            while (next_index===cur_index) {
                next_index = parseInt(Math.random()*num)
            }
        }
        // 3 : 
        console.log(next_index, ', ')
        state.cur_song = state.play_list[next_index]
        state.is_playing = true
    },
    // , mini--->>  : 
    big_player_hidden (state, payload) {
        state.big_show = false
        state.mini_show = true
    },
    // mini, --->>  : mini, 
    big_player_show (state, payload) {
        state.big_show = true
        state.mini_show = false
    },
    //  --->>  : <audio></audio>@timeupdate
    get_palyer_time (state, payload) {
        if (state.is_finger_moving) return  // , get_palyer_time()
        state.ing_time = payload.ing_time
        state.end_time = payload.end_time
    },
    // --->>  : @1:,  @2,@touchend
    turn_to_the_time (state, payload) {
        state.turn_time = payload
        state.is_finger_moving = false
    },
    // cd 
    rotate_cd (state, payload) {
        state.deg = payload
    }
}
// cur_songindex
//  :  cur_song = {songname, songmid, singers........}
//  :  list = [{}, {}, {}....]
function get_cur_song_index (cur_song, list) {
    let cur_song_index
    list.map((item, index)=>{
        if (item.songmid===cur_song.songmid) cur_song_index=index 
    })
    return cur_song_index
}
// index 
//  : index
//  : 
function get_next_song_index (cur_index, list) {
    let next_index = cur_index+1
    if (next_index>list.length-1) next_index=0
    return next_index
}
// --->> 
function no_repeat (list) {
    list = list.map(item => JSON.stringify(item))   // item
    list = [...new Set(list)]                       // 
    list = list.map(item => JSON.parse(item))       // stringobj
    return list
}

const detail={
    state,mutations
}

export default detail



